@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #ffffff;
}


.el{
    font-weight: 800;
    text-transform: capitalize;
}

h2:not(span){
    font-weight: 200;
    font-family: 'Libre Franklin', sans-serif;
    color: hsl(0, 0%, 59%);
}

span{
    color: black;
}
.header-content{
    margin-top: 1.7rem;
}

p{
    font-weight: 100;
    color: hsl(0, 0%, 59%);
}
input{
    width: 85%;
    margin: auto;
    border-radius: 36px;
    border: 1px solid #dee2e6;
    
    padding: 13px;
}
input::-webkit-input-placeholder{
    opacity: 50%;
}
input:-ms-input-placeholder{
    opacity: 50%;
}
input::placeholder{
    opacity: 50%;
}

input:focus-visible{
    outline: none;
}
.inputs{
    text-align: center;
}

.btn{
    width: 85%;
    border-radius: 36px;
    margin-top: 1rem;
    padding: .6rem;
    background-color:  hsl(223, 87%, 63%);
}

@media(min-width:768px){
    .inputs{
        display: flex;
        position: relative;
    }
    input[type=email]{
        width: 65%;
    }
    .btn{
        width: 30%;
        margin:0 1rem
    }
    .inputs > p{
        position: absolute;
        top: 100%;
        right: 50%;


    }
}

@media(min-width:992px){
    .container{
        max-width: 720px;
    }
}
.fa{
    font-size: 24px;
    margin-left: 1rem;
    color: hsl(223, 87%, 63%);
    
}

.fa-facebook-f{
    border: 1px solid;
    border-radius: 36px;
    padding: 7px 12px;
}
.fa-twitter{
    border: 1px solid;
    border-radius: 36px;
    padding: 7px 8px;
}
.fa-instagram{
    border: 1px solid;
    border-radius: 36px;
    padding: 7px 10px;
}
.fa-twitter,.fa-facebook-f,.fa-instagram{
    transition: all .25s ease-in-out;
}
.w-35{
    width: 35%;

}
footer div{
    margin: 1rem auto;
}

.fa-facebook-f:hover,.fa-instagram:hover,.fa-twitter:hover{
    color: white;
    background-color: hsl(223, 87%, 63%);;
    cursor: pointer;
}
